function hoverEffect(target) {
    const targetBox = document.querySelector(`.partners-box.${target}`);
    const otherBoxes = document.querySelectorAll(`.partners-box:not(.${target})`);

    targetBox.classList.add('scaled');
    otherBoxes.forEach(box => box.classList.add('hidden'));
}

function resetEffect(target) {
    const targetBox = document.querySelector(`.partners-box.${target}`);
    const otherBoxes = document.querySelectorAll(`.partners-box:not(.${target})`);

    targetBox.classList.remove('scaled');
    otherBoxes.forEach(box => box.classList.remove('hidden'));
}
